

@media screen and (min-width: 1024px) {
    .sidebar{
        background: rgba(0,177,183,0.9);
        position: absolute;
        left: 0pc;
        top: 0px;
        z-index: 3;
        display: block;
        width: 280px;
        height: 100%;
        text-align: left;
        box-shadow: 0px 1px 5px 0px rgba(0,80,83,1);
        &__cont{
            position: relative;
            width: 100%;
            display: block;
            padding: 125px 0 0 0;
        }
    }
    .content{
        padding-left: 300px;
    }
    .close-sidebar{
        .sidebar{ left: -226px; 
            display: block;
            .menu-sidebar{
                opacity: 0;
            }
        }
        .content{ padding-left: 74px; }
    }
    .sidebar-menu{
        display: block;
        padding: 0px 0 0 0;
        &__item{
            display: block;
        }
        &__link{
            font-size: 16px;
            line-height: 20px;
            color: #ffffff;
            padding: 12px 20px 12px 30px;
            text-shadow: 0px 1px 5px #005053;
            display: block;
            &:hover{
                background: #00d3db;
            }
        }
        &__item_active{
            .sidebar-menu__link{
                background: #00d3db;
            }
        }
    }
    .button-sidebar{
        position: absolute;
        right: 15px;
        top: 95px;
        height: 22px;
        width: 22px;
        .button-sidebar__cont {
            position: relative;
            display: block; }
        .line {
            display: block;
            position: absolute;
            height: 22px;
            width: 3px;
            background: #ffffff;
            border-radius: 0px;
            opacity: 1;
            left: 0;
            -webkit-transition: .35s ease-in-out;
            -moz-transition: .35s ease-in-out;
            -o-transition: .35s ease-in-out;
            transition: .35s ease-in-out;
            -webkit-border-radius: 0px;
            -moz-border-radius: 0px;
            border-radius: 0px; 
            box-shadow: 1px 2px 0px 0px rgba(0,0,0,0.5);
        }
        .line:nth-child(1) {
            left: 0px; }
        .line:nth-child(2) {
            left: 7px; }
        .line:nth-child(3) {
            left: 14px; }
        &.active .line:nth-child(1) {
            top: 0px;
            left: 0px;
            width: 100%;
            height: 3px;
        }
        &.active .line:nth-child(2) {
            top: 7px;
            left: 0px;
            width: 100%;
            height: 3px;
        }
        &.active .line:nth-child(3) {
            top: 14px;
            left: 0px;
            width: 100%;
            height: 3px;
        }
    }
    .footer-left{
        width: 330px;
    }
    .footer-center{
        padding: 0 345px 0 345px;
        margin-left: -335px;
        margin-right: -335px;
    }
    .box-social{
        width: 330px;
        &__item{
            width: 50px;
            height: 50px;
            font-size: 23px;
            line-height: 28px;
            padding-top: 14px;
        }
    }
    .copyright{
        padding-top: 27px;
    }
    .box-cont-exchange{
        left: auto;
        transform: translateX(0);
        right: 0px;
        top: auto;
        bottom: -30%;
    }
    .exchange-cont__name{
        font-weight: 600;
    }
    .box-cont__title{
        padding-left: 38px;
    }
    .content_long{
        padding-left: 0px!important;
        padding-top: 50px;
    }
    .sidebar_top{
        position: absolute;
        left: 0px;
        top: 80px;
        z-index: 99;
        width: 100%!important;
        display: block!important;
        background: #3c3c3c;
        border-top: solid 2px #c5c9c7;
        height: auto;
        .sidebar__cont{
            padding: 0px 0px 0px 0px;
            text-align: center;
        }
        .menu-sidebar{
            display: table;
            width: 100%;
        }
        .sidebar-menu__item{
            display: table-cell;
            padding: 10px 11px 10px 11px;
            &.active{
                .sidebar-menu__link{
                    color: #badaff;
                    background: none;
                }
            }
        }
        .sidebar-menu__link{
            padding: 0px;
            font-size: 14px;
            line-height: 18px;
            color: #ffffff;
            &:hover{
                color: #badaff;
                background: none;
            }
        }
    }
    .hide-desktop{
        display: none!important;
    }
    .list-webinars{
        &__item{
            width: 33.33%;
            margin-right: -5px;
        }
    }
}
