        transform: translate(0, 0);
        margin:  30px auto;
    }
    .text-info__img{        
        width: 355px;        
        margin-left: 25px;
    }
    
}
@media screen and (min-width: 1200px){
    .box-exchange{
        .btn{
            height: 44px;
            font-size: 18px;
            line-height: 22px;
            &_back{
                padding-left: 55px;
            }
            &__arr{
                width: 38px;
                font-size: 16px;
                padding-top: 12px;
            }
        }
    }
    .task-list{
        &__item{            
            padding-right: 145px;
            padding-left: 30px;            
        }
        &__number{            
            right:45px;            
        }
    }
    .task-summ{
        font-size: 30px;
        line-height: 35px;
        padding-bottom: 30px;
        padding-right: 45px;
    }
    .chat-messages-list{
        padding-top: 20px;
    }
    .chat-messages-list__img{
        display: inline-block;
        vertical-align: bottom;
        width: 64px;
        padding: 1px;
        border: 1px solid #e4e5e7;
        img{
            display: block;
            width: 100%;
        }
    }
    .chat-messages-list__cont{
        max-width: 549px;
        display: inline-block;
        width: calc(100% - 99px);
        margin-left: 30px;
        padding-right: 70px;
        padding-left: 22px;
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .chat-messages-list__item{
        padding-left: 0;
        padding-right: 0;
        padding-top: 10px;
        padding-bottom: 10px;
        display: flex;
        align-items: flex-end;
        &_right{
            justify-content: flex-end;
            .chat-messages-list__img{
                order: 2;
                margin-right: 0;
                margin-left: 30px;
            }
            .chat-messages-list__cont{
                order: 1;
                margin-left: 0;
            }
        }
    }
    .chat-messages-list__name{
        display: none;
    }
    .chat-messages-list__date{
        position: absolute;
        right: 20px;
        bottom: 10px;
        font-size: 16px;
        line-height: 19px;
    }
    
    
    
    
}
